<template>
  <div class="article-create">
    <h1>{{ id ? '编辑' : '新建'}}文章</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="所属分类">
        <el-select v-model="model.categories" multiple>
          <el-option
            v-for="item in categories"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="标题">
        <el-input v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="详情">
        <vue-editor useCustomImageHandler 
        @imageAdded="handleImageAdded" 
        v-model="model.body"></vue-editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { VueEditor } from 'vue2-editor'

export default {
  name: "article-create",
  props: {
    id: {}
  },
  data() {
    return {
      model: {},
      categories: []
    };
  },
  methods: {
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData();
      formData.append("file", file);
      const res = await this.$axios.post('upload', formData);
      Editor.insertEmbed(cursorLocation, "image", res.data.url);
      resetUploader();
  },
    async save() {
      let res;
      if (this.id) {
        // 更新
        res = await this.$axios.put(`rest/articles/${this.id}`, this.model);
        this.$router.push({ name: "articles-list" });
        this.$message({
          type: "success",
          message: "更新成功"
        });
      } else {
        // 新建
        res = await this.$axios.post("rest/articles", this.model);
        this.$router.push({ name: "articles-list" });
        this.$message({
          type: "success",
          message: "新建成功"
        });
      }
    },
    async fetch() {
      const res = await this.$axios.get(`rest/articles/${this.id}`);
      this.model = res.data;
    },
    async fetchCategories() {
      const res = await this.$axios.get(`rest/categories/`);
      this.categories = res.data;
    }
  },
  created() {
    this.fetchCategories();
    // 满足this.id才执行this.fetch()
    this.id && this.fetch();
  },
  components: {
      VueEditor
  }
};
</script>

